.container{
    width: 290px;
    height: 270px;
    margin-top: 10px;
    display: flex;
    border-radius: 15px;
    text-align: center;
    justify-content: space-between;
}

.text1{
    color:rgba(134, 132, 135, 1.00);
}
.text2{
    color: rgba(252, 255, 255, 1.00);
}
.wrapper{
    display: flex;
    width: 350px;
    margin-left: -30px;

}
@media screen and (max-width: 1024px){
   .container{
    width: 220px;
    height: 220px;
   } 
   .wrapper{
    background:rgba(30, 27, 30, 1.00);
border-radius: 25px;
height: 50px;
width: 320px;
margin-left: -10px;
   }
   .text1{
    margin-top: 10px;
   }
   .text2{
    margin-top: 10px;
   }
}

@media screen and (max-width:768px){
.container{
    width: 220px;
    height: 330px;
    margin-top: 10px;
}
.wrapper{
width: 220px;
height: 50px;
   margin-top: 70px;
margin-left:35px;
background:rgba(30, 27, 30, 1.00);
border-radius: 25px;
}
.text1 {
margin-top: 10px;
}
.text2{
    margin-top: 10px;
}
}
@media screen and (max-width: 480px){
    .container{
        width: 100px;
        height: 160px;
        margin-top: 10px;
        margin-left: -8px;
    }
    .wrapper{
        margin-top: -10px;
        width: 100px;
        margin-left: -7.5px;
    background: rgba(30, 27, 30, 1.00);
    height: 50px;
    border-radius: 15px;
    }
    .text1 {
        font-size: 1rem;
        margin-top: 5px;
    }
    .text2{
        font-size: 1rem;
        margin-top: 5px;
    }

}